// --- generator ---------------------------------------------------------------

@mixin break-point($args...){
    $first:null;
    $second:null;
    $third:null;
    @if(length($args) == 3){
        $first:nth($args, 1);
        $second:nth($args, 2);
        $third:nth($args, 3);
    }@else if(length($args) == 2){
        $first:nth($args, 1);
        $second:nth($args, 2);
    }@else if(length($args) == 1){
        $list-args:nth($args,1);
        $first:nth($list-args, 1);
        $second:nth($list-args, 2);
    }

    @if(type_of($second) == string){
        @if($second == min){
            @include min-screen($first){
                @content;
            }
        }@else if($second == max){
            @include max-screen($first){
                @content;
            }
        }@else if($second == minH){
            @include min-screen-height($first){
                @content;
            }
        }@else if($second == maxH){
            @include max-screen-height($first){
                @content;
            }
        }@else if($second == height){
            @include screen-height($first){
                @content;
            }
        }
    }@else if(type_of($second) == number){
        @include screen($first, $second, $third){
            @content;
        }
    }
}

@mixin mq($args...) {
    $media-type: 'only screen';
    $media-type-key: 'media-type';
    $args: keywords($args);
    $expr: '';

    @if map-has-key($args, $media-type-key) {
        $media-type: map-get($args, $media-type-key);
        $args: map-remove($args, $media-type-key);
    }

    @each $key, $value in $args {
        @if $value {
            $expr: "#{$expr} and (#{$key}: #{$value})";
        }
    }

    $temp : #{$media-type} #{$expr};

    @media #{$temp} {
        @content;
    }
}

// --- screen ------------------------------------------------------------------

@mixin screen($min, $max, $orientation: false) {
    @include mq($min-width: $min, $max-width: $max, $orientation: $orientation) {
        @content;
    }
}

@mixin max-screen($max) {
    @include mq($max-width: $max) {
        @content;
    }
}

@mixin min-screen($min) {
    @include mq($min-width: $min) {
        @content;
    }
}

@mixin screen-height($min, $max, $orientation: false) {
    @include mq($min-height: $min, $max-height: $max, $orientation: $orientation) {
        @content;
    }
}

@mixin max-screen-height($max) {
    @include mq($max-height: $max) {
        @content;
    }
}

@mixin min-screen-height($min) {
    @include mq($min-height: $min) {
        @content;
    }
}

// --- hdpi --------------------------------------------------------------------

@mixin hdpi($ratio: 1.3) {
    @media only screen and (-webkit-min-device-pixel-ratio: $ratio),
    only screen and (min-resolution: #{round($ratio*96)}dpi) {
        @content;
    }
}

// --- hdtv --------------------------------------------------------------------

@mixin hdtv($standard: '1080') {
    $min-width: false;
    $min-height: false;

    $standards: ('720p', 1280px, 720px)
            ('1080', 1920px, 1080px)
            ('2K', 2048px, 1080px)
            ('4K', 4096px, 2160px);

    @each $s in $standards {
        @if $standard == nth($s, 1) {
            $min-width: nth($s, 2);
            $min-height: nth($s, 3);
        }
    }

    @include mq(
            $min-device-width: $min-width,
            $min-device-height: $min-height,
            $min-width: $min-width,
            $min-height: $min-height
    ) {
        @content;
    }
}

// --- iphone4 -----------------------------------------------------------------

@mixin iphone4($orientation: false) {
    $min: 320px;
    $max: 480px;
    $pixel-ratio: 2;
    $aspect-ratio: '2/3';

    @include mq(
            $min-device-width: $min,
            $max-device-width: $max,
            $orientation: $orientation,
            $device-aspect-ratio: $aspect-ratio,
            $-webkit-device-pixel-ratio: $pixel-ratio
    ) {
        @content;
    }
}

